.experience-main-class {
    display: flex;
    height: 100%;
    flex-direction: column;
    margin-top: 2rem;
    gap: 1.5rem;
    overflow: hidden;
}

.experience-head-title {
    font-size: 35px;
    color: rgb(235, 233, 233);
    text-align: left;
    font-weight: 600;
}

.experience-carousel-container {
    display: flex;
    align-items: center;
    gap: 2rem;
    flex: 1;
    justify-content: center;
}

.carousel-arrow {
    background-color: transparent;
    border: 2px solid #30363d;
    color: #F0F6FC;
    width: 50px;
    height: 50px;
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    transition: all 0.3s ease;
    flex-shrink: 0;
}

.carousel-arrow:hover {
    background-color: #30363d;
    border-color: #58a6ff;
    color: #58a6ff;
    transform: scale(1.1);
}

.experience-carousel {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    min-height: 350px;
    max-width: 750px;
    overflow: hidden;
    position: relative;
}

.experience-box {
    width: 100%;
    max-width: 650px;
    border-radius: 8px;
    color: whitesmoke;
    animation: fadeInSmooth 1.2s ease-in-out;
}

@keyframes fadeInSmooth {
    0% {
        opacity: 0;
    }

    50% {
        opacity: 0.5;
    }

    100% {
        opacity: 1;
    }
}

.experience-head {
    font-size: 30px;
    color: rgb(235, 233, 233);
    text-align: left;
    font-weight: 600;
    margin: 0 0 1rem 0;
}

.experience-head-location {
    font-size: 20px;
    color: rgb(235, 233, 233);
    text-align: left;
    font-weight: 600;
    margin: 0 0 1rem 0;
}

.experience {
    font-size: 18px;
    margin-bottom: 20px;
    color: gainsboro;
    /* font-style: italic; */
    text-align: justify;
}

.experience-punch {
    color: white;
    font-style: normal;
}

.carousel-indicators-exp {
    display: flex;
    gap: 1rem;
    justify-content: center;
    align-items: center;
    margin-top: 1.5rem;
}

.indicator-exp {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background-color: #404854;
    cursor: pointer;
    transition: all 0.3s ease;
    border: none;
    display: inline-block;
}

.indicator-exp.active {
    background-color: #58a6ff;
    box-shadow: 0 0 8px #58a6ff;
    width: 40px;
    height: 4px;
    border-radius: 2px;
}

.indicator-exp:hover {
    background-color: #58a6ff;
}

/* Company gradient highlights */
.microsoft-text {
    background: linear-gradient(90deg,
            #f25022 0%,
            #7fba00 25%,
            #00a4ef 50%,
            #ffb900 75%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    font-weight: bold;
}

.amazon-text {
    background: linear-gradient(90deg,
            #ff9900 0%,
            #ffb84d 50%,
            #333333 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    font-weight: bold;
}

.optum-text {
    background: linear-gradient(90deg,
            #f58220 0%,
            #fbb040 50%,
            #ffc20e 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    font-weight: bold;
}

/* ðŸŒ Responsive Design for Large Screens (1441px and up) */
/* ðŸŒ Responsive Design for Large Screens (1441px and up) */
@media screen and (min-width: 1800px) {
    .experience-main-class {
        padding-left: 2rem;
        padding-right: 2rem;
        gap: 1rem;
    }

    .experience-head {
        font-size: 38px;
    }

    .experience-head-location {
        font-size: 24px;
    }

    .experience-box {
        width: 900px;
    }

    .experience {
        font-size: 20px;
        line-height: 1.75;
    }
}

/* ðŸ’» MEDIA QUERY FOR TABLETS */
@media screen and (max-width: 1024px) {
    .experience-head-title {
        font-size: 30px;
    }

    .experience-head {
        font-size: 26px;
    }

    .experience {
        font-size: 17px;
    }
}

/* ðŸ“± MEDIA QUERY FOR MOBILE DEVICES */
@media screen and (max-width: 768px) {
    .experience-head-title {
        font-size: 26px;
        /* Balanced header size */
    }

    .experience-box {
        max-width: 100%;
        width: 100%;
        margin-top: 0;
    }

    .experience-head {
        font-size: 22px;
        /* Clear section subhead */
        margin-bottom: 0.5rem;
    }

    .experience-head-location {
        font-size: 15px;
        margin-bottom: 0.5rem;
    }

    .experience {
        font-size: 15.5px;
        /* Increased from 14px */
        line-height: 1.6;
        text-align: justify;
    }

    .experience-carousel-container {
        flex-direction: column;
        gap: 0.5rem;
    }

    .experience-carousel {
        min-height: auto;
        width: 100%;
    }

    .carousel-arrow {
        display: none !important;
    }

    .experience-carousel-container .carousel-arrow {
        display: none !important;
    }

    .carousel-indicators-exp {
        order: 3;
        margin-top: auto;
        /* Push to bottom */
        margin-bottom: 0.5rem;
        /* Spacing from bottom edge/footer */
        gap: 0.5rem;
    }

    .experience-main-class {
        height: auto;
        overflow: visible;
        padding-bottom: 1rem;
    }
}